<header class="header"><div class="header-inner"><h1 class="title"><span>FINAL FANTAXY XIV</span><span>RAID CHARACTER CARD</span></h1></div></header><div class="main-contents"><div class="about-page" v-if="pageCount === 1"><div class="about-inner"><section><h2 class="title">このページについて</h2><p>FINAL FANTAXY XIVにおいて、高難易度レイド等のバトルコンテンツへ向けた自己紹介用のカードメーカーです。<br>The Lodestoneの募集記事と一緒にSNSに投稿するなど、固定パーティーを探す際の補助的な用途にご活用ください。</p></section><section><h2 class="title">カードサンプル</h2><p>このようなカードが作れます。</p><div class="card-image"><img :src="require('@/assets/images/card-sample.png')"></div></section><section><h2 class="title">動作確認環境</h2><p>Google Chrome(バージョン111.0.5563.147)<br>Microsoft Edge(バージョン111.0.1661.6)<br>Firefox、Safari等上記以外のブラウザや、Mac、スマートフォンでの動作に関しては保証致しかねます。</p></section><section><h2 class="title">ご利用にあたって</h2><p>当コンテンツは製作者のフロントエンド学習を目的として作られました。そのため、ゲーム内の最新の状態が反映されていない場合があります。<br>不具合、ご意見などございましたら、<a href="https://twitter.com/akitoki" target="_blank">@akitoki</a>までご連絡ください。<br>また当コンテンツにて作成したコンテンツを利用したことで利用者に何かしらのトラブルが発生したとしても、当方は責任を負いません。<br>以上同意していただけましたら下のボタンよりお進みください。</p></section><div class="create-button"><button class="card-create-button" @click="pageCountPlus">カードを作成する</button></div></div></div><div class="card-create" v-if="pageCount === 2"><InputContents></InputContents><cardPreview></cardPreview></div></div><footer class="footer"><div class="copyright">(C) SQUARE ENIX CO., LTD. All Rights Reserved.<br>Copyright © A-HEAD All right reserved.</div></footer>